import hexSha1 from 'hex-sha1';
import uniLoadMore from "@/components/uni-load-more.vue"
import {ChangePageTitle} from '../../../title.js'
export default {
	data() {
		return {
			title: '我的跟进记录',
			back1: true,
			accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			appsercert: 'MTExMTEx',
			sha: '',
			keys: [],
			page: 1,
			status: 'more',
			isShow: false,
			show:false
		}
	},
	components: {
		uniLoadMore
	},
	methods: {
		getlist() {
			var signStr =
				"access-token=" +
				this.accessToken +
				"&appsercert=" +
				this.appsercert +
				"&page=" +
				this.page;
			console.log(signStr)
			this.sha = hexSha1(signStr);
			console.log(this.sha)
			this.$axios(this.$baseUrl.myfollow,{
					'access-token': this.accessToken,
					sign: this.sha,
					page: this.page
				})
				.then(res => {
					console.log(res)
					// alert(JSON.stringify(res))
					if(res.data.items.length == 0){
						this.show = true
					}else{
						this.show = false
					}
					uni.hideLoading()
					// console.log(res.data._links.self.href.substring(res.data._links.self.href.length-1))
					if(res.data._links.self.href.substring(res.data._links.self.href.length-1)!=this.page){
						this.status = "noMore";
					} else {
						// console.log(res.data);
						this.keys = this.keys.concat(res.data.items);
						this.isShow = true
						// console.log(this.isShow)
					}
				}, (error) => {
					console.log(error)
				})
		},
		onReachBottom() {
			this.status = "loading";
			console.log("我被拉到底部了");
			// console.log(this.status)
			setTimeout(() => {
				this.page++;
				console.log(this.page);
				this.getlist();
			}, 2000)
		},
		todetail(id){
			uni.navigateTo({
				url:'/pages/followdetail/index?id='+id
			})
		}
	},
	
	onShow() {
		document.title = '我的跟进记录'
		ChangePageTitle('我的跟进记录')
	},
	created() {
		var list =  uni.getStorageSync('login')
		if(list){
			// alert(JSON.stringify(list))
			this.accessToken = list.access_token
			this.appsercert = list.appsercert
			// alert(this.appsercert)
			// alert(this.accessToken)
		}
		uni.showLoading({
			title: '加载中'
		})
		this.getlist()
		console.log('hahh')
	}
}
